.view-cover-letters-container {
    max-width: 1000px;
    margin: 20px auto;
    padding: 20px;
    background: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    font-family: Arial, sans-serif;
}

h1 {
    text-align: center;
    margin-bottom: 20px;
    color: #333333;
}

.cover-letters-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

.cover-letters-table th,
.cover-letters-table td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: left;
}

.cover-letters-table th {
    background-color: #f4f4f4;
    color: #333;
    font-weight: bold;
}

.cover-letters-table tr:nth-child(even) {
    background-color: #f9f9f9;
}

.cover-letters-table tr:hover {
    background-color: #f1f1f1;
}

.download-btn {
    padding: 5px 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.download-btn:hover {
    background-color: #0056b3;
}
